#calendar{
    margin-top: 8px;
display: flex;
    justify-content: center;
margin: auto;
text-align: center;
}
#projects h2{
    color: var(--color-heading);
}
#gitstats{
    gap:60px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin:auto;
    width: 85%;
}
#gitstats img{
    width: 100%;
    border-radius: 1rem;
    border: 5px solid #37b9f1;
}
#procard{
 
    width:85%;
    margin:auto;
    display: grid;
    gap:20px;
    grid-template-columns: repeat(1,1fr);
  }