nav {
    display: flex;
    position: fixed;
    background-color: rgba(26, 173, 199, 0.3);
    padding: 0.7rem 1.7rem;
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    display:none
  }
